import React, { Component } from 'react';
import Dialog from '@material-ui/core/Dialog';
import TextField from '@material-ui/core/TextField';
import { useHistory } from "react-router-dom";
import './signup.css';
import axios from 'axios';
import stringify from 'qs-stringify';
import Loader from 'react-loader-spinner';

 
function OtpVerify(props){
	return(
		          <div className='SignUpCont'>
                    <div className='signupheader'> Register Now</div>
                           <form onSubmit = {props.hs}>
                  { !props.otps ?  <div className='Mobile'> <TextField id="phone" label="Mobile" variant="outlined" fullWidth onChange={props.hc} type='number' /></div> : <div className='OTP'> <TextField id="otp" label="OPT" variant="outlined" fullWidth type='number' onChange={props.hc} /></div> }  
                    
                      <center>
                         <button type='submit' className='btn-login'>
                          { !props.otps ? "Send OTP" : "Verify OTP" }
                         </button>
                          { props.load ?  <Loader type="ThreeDots" color="#FF4949" height="50" width="50" /> : null }

                         </center>
                   </form>
                   

                     </div>
		)
}



















class SignUpForm extends Component {
    
 constructor(props) {
    super(props);
    this.state = {
    			  otptoken:props.otptoken,
    			  phone : props.phone,
    			  name : '',
    			  email : '',
    			  pass : '',
    			  loading: false,
    			  error : ''
    			};
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
}
handleChange(event) {
            this.setState({ [event.target.name] : event.target.value});
}

handleSubmit(event) {
        event.preventDefault();
    this.setState({'loading':true});

    axios.post(`https://cors-anywhere.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ signup:'',
    				email :this.state.email,
    				name : this.state.name,
    				pass : this.state.pass,
    				phone : this.state.phone,
    				otk : this.state.otptoken
    		        


    }))
    .then((response) =>{
    	console.log(response.data);
    	this.setState({'loading':false});
    	if(response.data.success){
    		console.log("user created");
        /* ----------------------After Validation Code---START-----------*/
        this.props.history.push('/login');
         /* ----------------------After Validation Code END--------------*/
    	}
    	else {
    		console.log("user not created");
    		console.log(this.state.otptoken);
    	}

    });

}

    render(){
    	return(
    		<div className='signupform'>
    		<div className='Form-header'>Complete Your SignUp </div>
    		<form onSubmit={this.handleSubmit}>
    		 <div className='Name'> <TextField  name='name' label="Name" variant="outlined" fullWidth onChange={this.handleChange} type='text' value={this.state.name} /></div>
    		 <div className='Email'> <TextField  name='email' label="Email" variant="outlined" fullWidth onChange={this.handleChange} type='email' value={this.state.email} /></div>
              <div className='Pass'> <TextField  name='pass' label="Password" variant="outlined" fullWidth onChange={this.handleChange} type='password' value={this.state.pass}  /></div>
            <button type='submit' className='btn-login'>Register</button>
             </form>
              
    		</div>
    		)
    }



}





class NameForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {phone: '',
    			  otp : '',
    			  otptoken: null,
    			  'otpsent':false,
    			  open : true,
    			  loading: false,
    			  error : '',
    			  otptokenset : false
    			};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleClose =  this.handleClose.bind(this);
	
  }

  handleChange(event) {
  	if(!this.state.otpsent){
    this.setState({phone: event.target.value});
}
else {
	this.setState({otp:event.target.value});
}
  }

  handleSubmit(event) {
   // alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
    this.setState({'loading':true});

    if(!this.state.otpsent) {
    axios.post(`https://cors-anywhere.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ sendOtp:'',
    		        'phone':this.state.phone


    }))
    .then((response) =>{this.setState({'otpsent':response.data.OTP_SENT});this.setState({'loading':false});console.log(response.data); });

}

else {
           axios.post(`https://cors-anywhere.herokuapp.com/http://imsammy.freevar.com/punit.php`,
    	stringify({ 'otpVerify':'',
    		        'otp':this.state.otp,
    		        'phone' : this.state.phone


    }))
    .then((response) =>{this.setState({'otptoken':response.data.otpToken});this.setState({'otptokenset':true});console.log("otp token recieved");console.log(response.data.otpToken);this.setState({'loading':false})});

}

  }

  handleClose(){
  	this.setState({'open':false});
    this.props.history.goBack();
  	
  }

  render() {


    return (
    
               	<div className='dialog'>
               	 <Dialog
                       open={this.state.open}
                       onClose={this.handleClose}
                       fullWidth
                       fullScreen
                       

                     >
                     <button className='signupback' onClick={this.handleClose}>Go Back </button>
                    <div className='container signupbox'>
                     <div className='row'>
                     <div className='col-6 leftbox'>
                     <div className='info'>
                        <div className='infohead'>INTWIFF </div>
                        <div className='infobody'> Rent Your Ride On The Go</div>
                     </div>
                       <div className='footer'>IntWiff ©2020 </div>
                     </div>
                     <div className='col-6 rightbox'>

     { this.state.otptokenset ? <SignUpForm otptoken={this.state.otptoken} phone = {this.state.phone} />  :  <OtpVerify hs={this.handleSubmit} hc={this.handleChange} otps = {this.state.otpsent} load = {this.state.loading} />  }
           


                     </div>


                     </div>
                    </div>


                     </Dialog>
               	</div>





    );
  }
}

export default NameForm;