import React, { Component } from "react";
import './NavBar.css';
import '../../../static/Grid.css';
import Login from '../login/login';
import NameForm from '../signup/Signup.js';
import { BrowserRouter, Switch, Route, Link } from "react-router-dom";
import Logout from '../logout/logout';
import DashBoard from '../dashboard/DashBoard';
import ls from 'local-storage';



function LoginSignUpButtons(){
  return(    <div className='nav-links-box'>
         <BrowserRouter>
  
       <Link
            to={{pathname: "/Login"}}
            className="nav-link"
          >
          Login
          </Link>

          <Link
            to={{pathname:'/Signup'}}
            className="nav-link"
          >
          signUp
          </Link>
 
           <Switch>
            
            <Route path="/Login" component={Login} />
            <Route path="/SignUp" component={NameForm} />

          </Switch>
          </BrowserRouter>
        
        </div>)
}

function DashBoardLogoutButtons(){
  return(    <div className='nav-links-box'>
         <BrowserRouter>
  
       <Link
            to={{pathname: "/Logout"}}
            className="nav-link"
          >
          Logout
          </Link>

          <Link
            to={{pathname:'/Dashboard'}}
            className="nav-link"
          >
          Dashboard
          </Link>
 
           <Switch>
            
            <Route path="/Logout" component={Logout} />
            <Route path="/Dashboard" component={DashBoard} />

          </Switch>
          </BrowserRouter>
        
        </div>)
}








class NavBar extends Component{
  constructor(props){
    super(props);
    this.state = {'isLoggedIn' : props.isLoggedIn}
  } 

componentDidUpdate() {

}


render() {


	return(

<div className='Nav row'> 
         
         <div className='col-6'>
         <div className='logo'>
             IntWiff
	    </div>
	    </div>
	    <div className='col-6'>
       {this.state.isLoggedIn ? <DashBoardLogoutButtons/> : <LoginSignUpButtons/>}
   
       
        </div>
    
</div>

		)
}
}


export default NavBar;
